<template>
  <header class="vff-header">
    <div class="f-container">
      <a href='/'><img src='./assets/logo.png' class="f-logo"/></a>
    </div>
  </header>
  <flow-form
    :questions="landingPageItems.concat(questions).concat(postQuizItems)"
    :standalone="true"
    @complete="onComplete"
    ref="vff"
  >
    <template #complete>
      <div class="f-section-wrap" v-if='!loading'>
        <span class="fh2">Way to go! Let's see how you did.</span>
        <span class="f-sub">
          <span>
            Teams that excel in these qualities have better cooperation, task performance, interpersonal relationships, overall satisfaction, and fewer conflicts.
          </span>
        </span>
        <!-- section 1 -->
        <div class="f-section-text quiz-results">
          <div class="left-col">
            <div class="results-header">Team Cohesion {{questionSetScores.cohesion.emoji}}</div>
          </div>
          <div class="right-col results-desc" v-html="questionSetScores.cohesion.text"></div>
        </div>
        <div class="results-break"></div>
        <!-- section 2 -->
        <div class="f-section-text quiz-results">
          <div class="left-col">
            <div class="results-header">Emotional Transactive Memory System (ETMS) {{questionSetScores.etms.emoji}}</div>
          </div>
          <div class="right-col results-desc" v-html="questionSetScores.etms.text"></div>
        </div>
        <div class="results-break"></div>
        <!-- section 3 -->
        <div class="f-section-text quiz-results">
          <div class="left-col">
            <div class="results-header">Cognitive Transactive Memory System (CTMS) {{questionSetScores.ctms.emoji}}</div>
          </div>
          <div class="right-col results-desc" v-html="questionSetScores.ctms.text"></div>
        </div>
        <div class="results-break"></div>
        <!-- section 4 -->
        <div class="f-section-text quiz-results">
          <div class="left-col">
            <div class="results-header">Team Satisfaction {{questionSetScores.satisfaction.emoji}}</div>
          </div>
          <div class="right-col results-desc" v-html="questionSetScores.satisfaction.text"></div>
        </div>
        <div class="results-break"></div>
        <div style="text-align: center">
          <!-- ending message -->
          <div class="ending-message" v-if="hasAtLeastOneBelowAverageResult">
            We recommend your team focus on strengthening and improving its culture. TourGuide Games will create culture-building strategies for your teamâ€™s unique needs using fun, unique gaming experiences. Book a demo now to find out how!
          </div>
          <div class="ending-message" v-else>
            We recommend your team focus on maintaining and building upon its strong culture. TourGuide Games creates tactical strategies to strengthen your culture through fun, customized gaming experiences. Book a demo now to find out how!
          </div>
          <a href="https://meetings.hubspot.com/larry-zoll">
            <button class="o-btn-action" aria-label="Book a demo!">
              <span>Book a demo!</span>
            </button>
          </a>
        </div>
        <br><br>
      </div>
      <div class="f-section-wrap" id="loading-dots" v-else>
        <h3 style="text-align: center;">Crunching the numbers...</h3>
        <div class="spinner">
          <div class="bounce1"></div>
          <div class="bounce2"></div>
          <div class="bounce3"></div>
        </div>
      </div>
    </template>
    <template #completeButton>
      <!-- we manually set the submit when completed fires, so this is just empty but needs _something_ -->
      <span style="display: none;"></span>
    </template>
  </flow-form>
</template>

<script>
  // Import necessary components and classes
  import { FlowForm, QuestionModel, QuestionType } from '@ditdot-dev/vue-flow-form'
  import choices from './components/choices'
  import interpretations from './components/interpretations'

  export default {
    name: 'quiz',
    components: {
      FlowForm
    },
    data() {
      return {
        landingPageItems: [
          new QuestionModel({
            title: "Let's get going!",
            subtitle: "Understanding the emotional and cultural health of your team is vital to itâ€™s long term success! To help maximize your teamâ€™s cooperation and performance, TourGuide Games has designed this short quiz that identifies your teamâ€™s strengths and areas to improve.",
            content: 'For each of the following 12 statements, select the response that best describes your team.',
            description: '',
            type: QuestionType.SectionBreak
          }),
        ],
        questions: [
          // set 1
          'There is a feeling of unity and cohesion in our team.',
          'Members of our team share a focus on work-related tasks.',
          'Members of our team feel close to each other.',
          // set 2
          'Our team understands each othersâ€™ emotional abilities.',
          'Team membersâ€™ emotional responses are consistent and accurate.',
          'There is authentic emotional communication between team members.',
          // set 3
          'Individual team members have specialized knowledge about aspects of our work.',
          'Our team members trust the credibility of each otherâ€™s knowledge about our work.',
          'Our team members know each other and work together in a well-coordinated fashion.',
          // set 4
          'Team members are satisfied with our overall work environment.',
          'Our team is satisfied with the way team members treat each other.',
          'Our team members are friendly and get along well.'
        ].map(question => {
          return new QuestionModel({
            title: question,
            type: QuestionType.MultipleChoice,
            helpTextShow: false,
						nextStepOnAnswer: true,
						required: true,
            options: choices.agreeRange(),
            scored: true
          })
        }),
        postQuizItems: [
          new QuestionModel({
            title: "One last thingâ€” what's your email?",
            subtitle: "Let's keep in touch!",
            placeholder: "admin@tourguidegames.com",
            type: QuestionType.Email,
            required: true
          })
        ],
        loading: true,
        questionSetScores: {
          cohesion: {},
          etms: {},
          ctms: {},
          satisfaction: {}
        },
        hasAtLeastOneBelowAverageResult: false
      }
    },
    methods: {
      onComplete(_completed, questionList) {
        const email = questionList.find(question => question.type === 'FlowFormEmailType')?.answer
        const scoredQuestions = questionList.filter(question => question.scored) // only ones that we marked to count for score
        const questionsAndAnswersText = scoredQuestions.map(question => `${question.title}: ${question.answer}`).join('\n')

        // send data to hubspot
        if (window.location.search.includes('noSendData')) {
          alert(`this will be sent to hubspot:\n${email}\n${questionsAndAnswersText}`)
        } else {
          this.submitHubspotForm(email, questionsAndAnswersText)
        }

        // score data to display on final page. indexes are inclusive of both start and end.
        this.questionSetScores = {
          cohesion: this.scoreToWords(this.avgScoreByIndex(scoredQuestions, 0, 2), 'cohesion'),
          etms: this.scoreToWords(this.avgScoreByIndex(scoredQuestions, 3, 5), 'etms'),
          ctms: this.scoreToWords(this.avgScoreByIndex(scoredQuestions, 6, 8), 'ctms'),
          satisfaction: this.scoreToWords(this.avgScoreByIndex(scoredQuestions, 9, 11), 'satisfaction')
        }

        // mark form as submitted, prevents navigation and removes the "you have unsaved changes" dialog when you close the tab
        this.$refs.vff.$data.submitted = true

        // wait 3 secs for cute loading gif
				setTimeout(() => this.loading = false, 3000)
      },
      avgScoreByIndex(scoredQuestions, startIndex, endIndex) {
        // a subset of the questions array that has everything from the start to end index, including the end, and get answers.
        const answerSubset = scoredQuestions.slice(startIndex, endIndex + 1).map(question => question.answer)

        // sum the scores of these questions and divide by the range of the index to get an average score of these questions
        return answerSubset.reduce((a, b) => a + b, 0) / (endIndex - startIndex + 1)
      },
      scoreToWords(score, section) {
        if (score < 3) {
          // if at least one result is below average we show different ending message, so we'll flag it here.
          this.hasAtLeastOneBelowAverageResult = true
          return {text: interpretations[section]['poor'], emoji: 'ðŸ˜«'}
        } else if (score < 4) {
          return {text: interpretations[section]['moderate'], emoji: 'ðŸ˜'}
        } else {
          return {text: interpretations[section]['excellent'], emoji: 'ðŸ˜ƒ'}
        }
      },
      submitHubspotForm(email, questionsAndAnswersText) {
        // account must have specified form already created, form must have `email` and `message` fields
        const hsAccountId = 20163534
        const hsFormId = 'c0a4a1e5-d4c4-4275-91b9-75e6a0c05591'

        const data = {
          fields: [
            {
              objectTypeId: 'contact',
              name: 'email',
              value: email
            },
            {
              objectTypeId: 'contact',
              name: 'message',
              value: questionsAndAnswersText
            }
          ],
          context: {
            hutk: document.cookie.replace(/(?:(?:^|.*;\s*)hubspotutk\s*=\s*([^;]*).*$)|^.*$/, '$1'),
            pageUri: window.location.href,
            pageName: 'How Healthy is Your Team?'
          }
        }

        const xhr = new XMLHttpRequest()
        xhr.open('POST', `https://api.hsforms.com/submissions/v3/integration/submit/${hsAccountId}/${hsFormId}`)
        xhr.setRequestHeader('Content-Type', 'application/json')

        xhr.onreadystatechange = function() {
          if(xhr.readyState == 4 && xhr.status == 200) { 
            console.log(xhr.responseText) // Returns a 200 response if the submission is successful.
          } else if (xhr.readyState == 4 && xhr.status == 400) { 
            console.error(xhr.responseText) // Returns a 400 error the submission is rejected.          
          } else if (xhr.readyState == 4 && xhr.status == 403) { 
            console.error(xhr.responseText) // Returns a 403 error if the portal isn't allowed to post submissions.           
          } else if (xhr.readyState == 4 && xhr.status == 404) { 
            console.error(xhr.responseText) //Returns a 404 error if the formGuid isn't found     
          }
        }

        // Sends the request
        xhr.send(JSON.stringify(data))
      }
    }
  }
</script>

